:root {
  --primary-color: #4361ee;
  --secondary-color: #3f37c9;
  --accent-color: #4cc9f0;
  --light-color: #f8f9fa;
  --dark-color: #212529;
  --gray-color: #6c757d;
  --success-color: #28a745;
  --danger-color: #dc3545;
  --warning-color: #ffc107;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

.admin-dashboard {
  display: flex;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fa;
}

.admin-dashboard .sidebar {
  width: 250px;
  background-color: var(--dark-color);
  color: white;
  padding: 1.5rem 1rem;
  position: fixed;
  height: 100vh;
  transition: var(--transition);
  z-index: 1000;
}

.admin-dashboard .logo {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  padding-left: 0.5rem;
  color: white;
}

.admin-dashboard .sidebar nav ul {
  list-style: none;
  padding: 0;
}

.admin-dashboard .sidebar nav ul li {
  padding: 0.8rem 1rem;
  margin-bottom: 0.5rem;
  border-radius: var(--border-radius);
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.8rem;
  transition: var(--transition);
}

.admin-dashboard .sidebar nav ul li:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.admin-dashboard .sidebar nav ul li.active {
  background-color: var(--primary-color);
  font-weight: 500;
}

.admin-dashboard .main-content {
  flex: 1;
  margin-left: 250px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.admin-dashboard .top-nav {
  background-color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--box-shadow);
  position: sticky;
  top: 0;
  z-index: 100;
}

.admin-dashboard .search-bar {
  display: flex;
  align-items: center;
  background-color: var(--light-color);
  border-radius: var(--border-radius);
  padding: 0.5rem 1rem;
  width: 300px;
}

.admin-dashboard .search-bar input {
  border: none;
  background: transparent;
  margin-left: 0.5rem;
  width: 100%;
  outline: none;
}

.admin-dashboard .admin-actions {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.admin-dashboard .notification-btn {
  background: none;
  border: none;
  font-size: 1.2rem;
  color: var(--gray-color);
  position: relative;
  cursor: pointer;
}

.admin-dashboard .badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: var(--danger-color);
  color: white;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.admin-dashboard .admin-profile {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.admin-dashboard .admin-profile svg {
  font-size: 1.5rem;
}

.admin-dashboard .content-area {
  flex: 1;
  padding: 2rem;
  background-color: #f5f7fa;
}
  
  /* Dashboard Sections */
  .dashboard-section h2,
  .users-section h2,
  .workouts-section h2,
  .equipment-section h2,
  .analytics-section h2 {
    color: var(--dark-color);
    margin-bottom: 1.5rem;
  }
  
  .welcome-banner {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: white;
    padding: 1.5rem;
    border-radius: var(--border-radius);
    margin-bottom: 2rem;
  }
  
  .welcome-banner h3 {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
  }
  
  .stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .stat-card {
    background-color: white;
    border-radius: var(--border-radius);
    padding: 1.5rem;
    box-shadow: var(--box-shadow);
    text-align: center;
  }
  
  .stat-card h3 {
    color: var(--gray-color);
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }
  
  .stat-card p {
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--dark-color);
  }
  
  .recent-activity {
    background-color: white;
    border-radius: var(--border-radius);
    padding: 1.5rem;
    box-shadow: var(--box-shadow);
  }
  
  .recent-activity h3 {
    margin-bottom: 1rem;
  }
  
  .recent-activity ul {
    list-style: none;
    padding: 0;
  }
  
  .recent-activity li {
    padding: 0.5rem 0;
    border-bottom: 1px solid #eee;
  }
  
  /* Users Section */
  .users-table {
    width: 100%;
    border-collapse: collapse;
    background-color: white;
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: var(--box-shadow);
  }
  
  .users-table th,
  .users-table td {
    padding: 1rem;
    text-align: left;
    border-bottom: 1px solid #eee;
  }
  
  .users-table th {
    background-color: var(--light-color);
    font-weight: 600;
  }
  
  .status-badge {
    padding: 0.3rem 0.6rem;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 500;
  }
  
  .status-badge.active {
    background-color: rgba(40, 167, 69, 0.1);
    color: var(--success-color);
  }
  
  .status-badge.inactive {
    background-color: rgba(220, 53, 69, 0.1);
    color: var(--danger-color);
  }
  
  .action-btn {
    padding: 0.3rem 0.8rem;
    border: none;
    border-radius: var(--border-radius);
    cursor: pointer;
    font-size: 0.8rem;
    margin-right: 0.5rem;
    transition: var(--transition);
  }
  
  .action-btn.edit {
    background-color: rgba(0, 123, 255, 0.1);
    color: #007bff;
  }
  
  .action-btn.delete {
    background-color: rgba(220, 53, 69, 0.1);
    color: var(--danger-color);
  }
  
  .action-btn:hover {
    opacity: 0.8;
  }
  
  /* Workouts Section */
  .workouts-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1.5rem;
  }
  
  .workout-card {
    background-color: white;
    border-radius: var(--border-radius);
    padding: 1.5rem;
    box-shadow: var(--box-shadow);
  }
  
  .workout-card h3 {
    margin-bottom: 1rem;
    color: var(--primary-color);
  }
  
  .workout-card p {
    margin-bottom: 0.5rem;
    color: var(--gray-color);
  }
  
  .workout-actions {
    margin-top: 1rem;
    display: flex;
    gap: 0.5rem;
  }
  
  .add-btn {
    background-color: var(--primary-color);
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: var(--border-radius);
    cursor: pointer;
    transition: var(--transition);
    margin-bottom: 1.5rem;
  }
  
  .add-btn:hover {
    background-color: var(--secondary-color);
  }
  
  .edit-btn, .delete-btn {
    padding: 0.3rem 0.8rem;
    border: none;
    border-radius: var(--border-radius);
    cursor: pointer;
    font-size: 0.8rem;
    transition: var(--transition);
  }
  
  .edit-btn {
    background-color: rgba(0, 123, 255, 0.1);
    color: #007bff;
  }
  
  .delete-btn {
    background-color: rgba(220, 53, 69, 0.1);
    color: var(--danger-color);
  }
  
  /* Equipment Section */
  .equipment-form {
    display: flex;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .equipment-form input {
    padding: 0.5rem 1rem;
    border: 1px solid #ddd;
    border-radius: var(--border-radius);
  }
  
  .equipment-list {
    background-color: white;
    border-radius: var(--border-radius);
    padding: 1rem;
    box-shadow: var(--box-shadow);
  }
  
  .equipment-item {
    display: flex;
    align-items: center;
    padding: 0.8rem 0;
    border-bottom: 1px solid #eee;
  }
  
  .equipment-icon {
    margin-right: 1rem;
    font-size: 1.2rem;
  }
  
  .equipment-name {
    flex: 1;
  }
  
  /* Analytics Section */
  .chart-placeholder {
    background-color: white;
    border-radius: var(--border-radius);
    padding: 1.5rem;
    box-shadow: var(--box-shadow);
    margin-top: 2rem;
    text-align: center;
  }
  
  .mock-chart {
    height: 300px;
    background-color: #f8f9fa;
    border-radius: var(--border-radius);
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--gray-color);
  }
  
  /* Responsive Design */
  @media (max-width: 992px) {
    .sidebar {
      width: 200px;
    }
    
    .main-content {
      margin-left: 200px;
    }
  }
  
  @media (max-width: 768px) {
    .sidebar {
      transform: translateX(-100%);
      width: 250px;
    }
    
    .sidebar.active {
      transform: translateX(0);
    }
    
    .main-content {
      margin-left: 0;
    }
    
    .stats-grid {
      grid-template-columns: repeat(2, 1fr);
    }
    
    .search-bar {
      width: 200px;
    }
  }
  
  @media (max-width: 576px) {
    .stats-grid {
      grid-template-columns: 1fr;
    }
    
    .search-bar {
      display: none;
    }
    
    .top-nav {
      padding: 1rem;
    }
    
    .content-area {
      padding: 1rem;
    }
    
    .workouts-grid {
      grid-template-columns: 1fr;
    }
  }