/* Equipment.css */
.equipment-suggestions {
    margin: 2rem 0;
    padding: 1.5rem;
    background-color: #f8f9fa;
    border-radius: 10px;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .equipment-suggestions h3 {
    margin-bottom: 0.5rem;
    color: #2d3748;
    font-size: 1.5rem;
    text-align: center;
  }
  
  .subtext {
    color: #718096;
    margin-bottom: 1.5rem;
    font-size: 0.9rem;
    text-align: center;
  }
  
  /* Category Filters */
  .category-filters {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .category-btn {
    padding: 0.5rem 1rem;
    background-color: #e2e8f0;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 0.8rem;
    transition: all 0.3s ease;
  }
  
  .category-btn:hover {
    background-color: #cbd5e0;
  }
  
  .category-btn.active {
    background-color: #4361ee;
    color: white;
  }
  
  /* Equipment Grid */
  .equipment-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .equipment-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background-color: white;
    border: 2px solid #e2e8f0;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    min-height: 100px;
  }
  
  .equipment-card:hover {
    border-color: #cbd5e0;
    transform: translateY(-2px);
  }
  
  .equipment-card.selected {
    border-color: #4361ee;
    background-color: #f0f4ff;
    box-shadow: 0 4px 6px rgba(67, 97, 238, 0.1);
  }
  
  .equipment-icon {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  .equipment-label {
    font-size: 0.9rem;
    font-weight: 500;
    color: #2d3748;
    text-align: center;
  }
  
  /* Selected Summary */
  .selected-summary {
    text-align: center;
    margin-bottom: 1rem;
    color: #4361ee;
    font-weight: 500;
  }
  
  /* Submit Button */
  .submit-btn {
    display: block;
    margin: 1.5rem auto 0;
    padding: 0.8rem 2rem;
    background-color: #4361ee;
    color: white;
    border: none;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1rem;
  }
  
  .submit-btn:hover {
    background-color: #3a56d4;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .submit-btn:disabled {
    background-color: #a0aec0;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .equipment-grid {
      grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    }
    
    .equipment-card {
      min-height: 90px;
      padding: 0.8rem;
    }
    
    .equipment-icon {
      font-size: 1.8rem;
    }
  }
  
  @media (max-width: 480px) {
    .equipment-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  
    .category-filters {
      gap: 0.3rem;
    }
  
    .category-btn {
      padding: 0.4rem 0.8rem;
      font-size: 0.7rem;
    }
  }