/* Header.css */
:root {
    --primary-color: #4361ee;
    --secondary-color: #3f37c9;
    --white: #ffffff;
    --border-radius: 8px;
    --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    --transition: all 0.3s ease;
  }
  
  /* Header Styles */
  .app-header {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: var(--white);
    padding: 1rem 0;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: var(--box-shadow);
    width: 100%;
  }
  
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
  }
  
  .logo-link {
    text-decoration: none;
  }
  
  .logo {
    font-size: 1.8rem;
    font-weight: 700;
    color: var(--white);
    letter-spacing: 1px;
    transition: var(--transition);
  }
  
  .logo:hover {
    opacity: 0.9;
  }
  
  /* Mobile Menu Button */
  .menu-toggle {
    background: none;
    border: none;
    cursor: pointer;
    display: none;
    padding: 0.5rem;
    z-index: 1001;
  }
  
  .menu-icon {
    display: block;
    width: 25px;
    height: 2px;
    background-color: var(--white);
    position: relative;
    transition: var(--transition);
  }
  
  .menu-icon::before,
  .menu-icon::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: var(--white);
    transition: var(--transition);
  }
  
  .menu-icon::before {
    top: -8px;
  }
  
  .menu-icon::after {
    top: 8px;
  }
  
  .menu-icon.open {
    background: transparent;
  }
  
  .menu-icon.open::before {
    transform: rotate(45deg);
    top: 0;
  }
  
  .menu-icon.open::after {
    transform: rotate(-45deg);
    top: 0;
  }
  
  /* Navigation Links */
  .nav-links {
    display: flex;
    gap: 1.5rem;
    align-items: center;
  }
  
  .nav-links a {
    color: var(--white);
    text-decoration: none;
    font-weight: 500;
    padding: 0.5rem 1rem;
    border-radius: var(--border-radius);
    transition: var(--transition);
  }
  
  .nav-links a:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  /* Mobile Styles */
  @media (max-width: 768px) {
    .menu-toggle {
      display: block;
    }
  
    .nav-links {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 2rem;
      transform: translateX(100%);
      transition: transform 0.3s ease-in-out;
      z-index: 1000;
    }
  
    .nav-links.active {
      transform: translateX(0);
    }
  
    .header-container {
      padding: 0 1.5rem;
    }
  
    .logo {
      font-size: 1.5rem;
    }
  }
  
  /* Small mobile devices */
  @media (max-width: 480px) {
    .header-container {
      padding: 0 1rem;
    }
  }